.admin-messages-container {
  padding: 2rem;
  margin: 0 auto;
  max-width: 1200px;
}

.msg-recived {
  color: black;
}

.message-list {
  list-style: none;
  padding: 0;
}

.message-card {
  background-color: #f9f9f9;
  border-left: 4px solid #f39c12;
  padding: 1rem;
  margin-bottom: 1rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  word-wrap: break-word;
  overflow-wrap: break-word;
}

.message-content {
    white-space: pre-wrap;
}

.date {
  font-size: 0.9rem;
  color: gray;
}

.delete-button {
  margin-top: 10px;
  padding: 6px 12px;
  background-color: #e67e22;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.delete-button:hover {
  background-color: #d35400;
}

@media (max-width: 768px) {
  .admin-messages-container {
    padding: 1rem;
  }

  
}
