@import '../../Card/section-header.css';


.product-results-container {
    padding: 20px;
    background-color: #f9f9f9; 
    max-width: 1200px;
    margin: 0 auto;
}

.product-results {
    display: grid;
    grid-template-columns: repeat(4, 1fr); 
    gap: 30px;
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
}

.product-results img {
    width: 80%;
    height: 200px;
    object-fit: contain; 
    border-radius: 8px;
    background-color: #f8f8f8;
    padding: 10px;
}

.pagination2 {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    gap: 10px;
}

.pagination2 button {
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 8px 14px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: 0.3s;
}

.pagination2 button:hover {
    background-color: #f39c12;
    color: white;
    border-color: #f39c12;
}

.pagination2 .active-page {
    background-color: #f39c12;
    color: white;
    border-color: #f39c12;
}

.pagination2 .ellipsis {
    padding: 8px 14px;
    font-weight: bold;
    color: #555;
}

.pagination2 {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    gap: 10px;
}

.pagination2 button {
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 8px 14px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: 0.3s;
    display: flex; /* За подравняване на иконата и текста */
    align-items: center;
    gap: 5px; /* Разстояние между иконата и текста */
}

.pagination2 button:hover {
    background-color: #f39c12;
    color: white;
    border-color: #f39c12;
}

.pagination2 .active-page {
    background-color: #f39c12;
    color: white;
    border-color: #f39c12;
}

/* Добавени стилове за новите елементи */
.pagination2 button:disabled {
    background-color: #f0f0f0;
    color: #ccc;
    cursor: not-allowed;
    border-color: #e0e0e0;
}

.pagination2 .ellipsis {
    padding: 8px 14px;
    font-weight: bold;
    color: #555;
}


.pagination2 button i {
    font-size: 14px;
}

@media (max-width: 768px) {
    .product-results {
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
        padding: 10px;
    }

    .product-card, .product2 { 
        width: 90%;
        max-width: 90%;
        max-height: 90%;
    }

    .product-results img {
        width: 90%;
        height: 100px; 
        object-fit: contain;
        padding: 6px;
    }

    .product-title {
        font-size: 1rem;
    }

    .product-price2 {
        font-size: 0.95rem;
    }
}